<template>
  <el-dialog
      :title="$t('modifySex.part1')"
      :visible.sync="dialogVisible"
      :close-on-click-modal=false
      :close-on-press-escape=false
      :before-close="handleCloseDialog"
      width="490px" class="sex-modify-dialog">
      <el-form :model="ruleForm" :rules="ruleFormRules" ref="ruleForm" label-width="130px">
        <!-- <el-form-item :label="$t('form.sex')" prop="sex">
          <el-input v-model.trim="ruleForm.sex" :placeholder="$t('form.sexPlaceholder')" maxlength="10"></el-input>
        </el-form-item> -->
        <el-radio-group v-model="ruleForm.sex">
          <el-radio label="男">{{$t('modifySex.part2')}}</el-radio>
          <el-radio label="女">{{$t('modifySex.part3')}}</el-radio>
          <el-radio label="保密">{{$t('modifySex.part4')}}</el-radio>
        </el-radio-group>
        <div class="btn-group appeal-btn">
          <el-button type="primary"  @click="formSubmit('ruleForm')">{{ $t('dialog.sure') }}</el-button>
        </div>
      </el-form>
    </el-dialog>
</template>

<script>
  import { editSex } from '@/api/user'

  export default {
    props: ['dialogVisible'],
    data() {
      return {
        ruleForm: {
          sex: '保密'
        },
        ruleFormRules: {
          // sex: [
          //   { required: true, message: this.$t('errorTips.noNickname'), trigger: 'blur' }
          // ]
        }
      }
    },
    methods: {
      handleCloseDialog() {
        this.$emit('closeDialog', 'sexModifyDialogVisible')
      },
      formSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const data = { ...this.ruleForm }
            editSex(data).then(res => {
              console.log('res', res)
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }
              this.$message.success(this.$t('modifySex.part5'))
              this.handleCloseDialog()
              this.$emit('getUserInfo')
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  .sex-modify-dialog{
    margin-bottom: 15px;
    /deep/.el-radio-group{
      margin: 0 auto;
    }
    label{
      width: 86px;
      display: inline-block;
      text-align: right;
      padding-right: 10px;
    }
    
    .code-tips{
      margin-top: 89px;
      color: #b4b6be;
      font-size: 12px;
    }
  }
</style>
<style lang="less">
  .sex-modify-dialog{
    .avatar-uploader{
      width: 109px;
      display: inline-block;
    }
    .avatar-uploader .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
      border-color: #409EFF;
    }
    .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 109px;
      height: 109px;
      line-height: 109px;
      text-align: center;
    }
    .avatar {
      width: 109px;
      height: 109px;
      display: block;
    }
    .el-button--primary{
      margin-left: 70px;
      margin-top: 35px;
    }
  }
</style>


